
    import {
        defineComponent,
        reactive,
        onMounted
    } from "vue";
    import {
        apiGetCopyright,
        apiSetCopyright
    } from "@/api/setting"
    import FooterBtns from '@/components/footer-btns/index.vue'
    export default defineComponent({
        components: {
            FooterBtns
        },
        setup() {
            // 表单数据
            const formData = reactive({
                info: '', // 版权信息
                icp_number: '', // icp备案号
                icp_link: '', // icp备案链接
                public_number: '', // 公安备案号
                public_link: '', // 公安备案链接
            })

            // 表单验证
            const rules = {
                // info: [
                //     {
                //         required: true,
                //         message: '请输入版权信息',
                //         trigger: ['blur'],
                //     },
                // ],
            }

            // 获取备案信息
            const getCopyright = () => {
                apiGetCopyright()
                    .then((res: any) => {
                        console.log('res', res)
                        formData.info = res.info
                        formData.icp_number = res.icp_number
                        formData.icp_link = res.icp_link
                        formData.public_number = res.public_number
                        formData.public_link = res.public_link
                    })
            }

            // 设置备案信息
            const setCopyright = () => {
                apiSetCopyright({
                        info: formData.info,
                        icp_number: formData.icp_number,
                        icp_link: formData.icp_link,
                        public_number: formData.public_number,
                        public_link: formData.public_link,
                    })
                    .then((res: any) => {
                        console.log('res', res)
                        getCopyright()
                    })
                    .catch((err: any) => {
                        console.log('err', err)
                    })
            }

            onMounted(() => {
                getCopyright()
            })

            return {
                formData,
                rules,
                getCopyright,
                setCopyright,
            }
        },
    })
